import React, { Component } from 'react';
import './card-component-styles.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class Card extends Component {
  constructor() {
    super();
    this.state = {
      isHovering: false,
    };
  }
  handleMouseHover = () => {
    this.setState(this.toggleHoverState);
  };

  toggleHoverState = (state) => {
    return {
      isHovering: !state.isHovering,
    };
  };
  render() {
    const { url, id, name, likes, comments } = this.props.card;
    return (
      <React.Fragment>
        <div className="card" onMouseEnter={this.handleMouseHover} onMouseLeave={this.handleMouseHover}>
          <img src={`${url}-${id}.png`} alt={name} />
          {this.state.isHovering && <div><FontAwesomeIcon icon="heart" className="icon" />{likes} <FontAwesomeIcon icon="comments" className="icon" />{comments}</div>}
        </div>
        
        
      </React.Fragment>
    );
  }
}
export default Card;
